import axios from 'axios';

const apiClient = axios.create({
  baseURL: `${process.env.VUE_APP_STRAPI_URL}/api`,
  headers: {
    'Content-Type': 'application/json',
    Authorization: `Bearer ${process.env.VUE_APP_STRAPI_TOKEN}`
  }
});

export default {
  getCampaigns() {
    return apiClient.get('/campaigns');
  },
  getCatalogs() {
    return apiClient.get('/supports?populate=*');
  },
  getCatalog(id) {
    return apiClient.get(`/supports/${id}`, {
      params: {
        populate: {
          pages: {
            populate: {
              products: {
                populate: ['image']
              },
              active_image_version: {
                populate: ['fichier_jpeg']
              }
            }
          }
        }
      }
    });
  },
  getCatalogWithNotes(id) {
    return apiClient.get(`/supports/${id}`, {
      params: {
        populate: {
          pages: {
            populate: {
              products: {
                populate: ['image']
              },
              notes: {
                populate: ['attachment', 'user']
              },
              active_image_version: {
                populate: ['fichier_jpeg']
              }
            }
          }
        }
      }
    });
  },
  createNote(data) {
    return apiClient.post('/notes', { data });
  },
  updateNote(id, data) {
    return apiClient.put(`/notes/${id}`, { data });
  },
  deleteNote(id) {
    return apiClient.delete(`/notes/${id}`);
  },
  upload(file) {
    const formData = new FormData();
    formData.append('files', file);
    return apiClient.post('/upload', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    });
  },
  login(identifier, password) {
    return apiClient.post('/auth/local', {
      identifier,
      password,
    });
  }
};