<template>
  <div class="login-container">
    <h2>Connexion</h2>
    <form @submit.prevent="handleLogin">
      <div class="form-group">
        <label for="identifier">Email ou Nom d'utilisateur:</label>
        <input type="text" id="identifier" v-model="identifier" required />
      </div>
      <div class="form-group">
        <label for="password">Mot de passe:</label>
        <input type="password" id="password" v-model="password" required />
      </div>
      <button type="submit">Se connecter</button>
    </form>
    <p v-if="error" class="error-message">{{ error }}</p>
  </div>
</template>

<script>
import strapiApi from '../services/strapiApi';

export default {
  name: 'LoginView',
  data() {
    return {
      identifier: '',
      password: '',
      error: null,
    };
  },
  methods: {
    async handleLogin() {
      this.error = null;
      try {
        const response = await strapiApi.login(this.identifier, this.password);
        console.log('Login successful:', response.data);
        localStorage.setItem('jwt', response.data.jwt); // Store JWT
        localStorage.setItem('user', JSON.stringify(response.data.user)); // Store user info
        this.$router.push('/'); // Redirect to home on success
      } catch (err) {
        this.error = err.response?.data?.error?.message || 'Une erreur est survenue lors de la connexion.';
        console.error('Login error:', err.response?.data || err);
      }
    },
  },
};
</script>

