import { createRouter, createWebHistory } from 'vue-router'

import LoginView from '../views/LoginView.vue'

const routes = [
  {
    path: '/',
    name: 'CatalogList',
    component: () => import('../views/CatalogList.vue')
  },
  {
    path: '/catalog/:id',
    name: 'CatalogDetail',
    component: () => import('../views/CatalogDetail.vue'),
    props: true
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginView
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  const isAuthenticated = localStorage.getItem('jwt');
  const requiresAuth = to.name !== 'Login'; // All routes except Login require auth

  if (requiresAuth && !isAuthenticated) {
    next({ name: 'Login' }); // Redirect to login page
  } else if (to.name === 'Login' && isAuthenticated) {
    next({ name: 'CatalogList' }); // Redirect to home if already logged in and trying to access login
  } else {
    next(); // Proceed to route
  }
});

export default router