<template>
  <div v-if="isLoggedIn" class="d-flex align-items-center ms-auto">
    <div class="dropdown">
      <a class="nav-link dropdown-toggle text-white" href="#" id="navbarDropdown" role="button" @click="toggleDropdown" aria-expanded="false">
        <i class="bi bi-person-circle me-1"></i> {{ user.username || user.email }}
      </a>
      <ul class="dropdown-menu dropdown-menu-end" :class="{ 'show': showDropdown }" aria-labelledby="navbarDropdown">
        <li><a class="dropdown-item" href="#" @click.prevent="logout">DÃ©connexion</a></li>
        <!-- Other options can be added here later -->
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserMenu',
  data() {
    return {
      user: null,
      isLoggedIn: false,
      showDropdown: false,
    };
  },
  created() {
    this.checkLoginStatus();
  },
  watch: {
    $route: 'checkLoginStatus' // Watch for route changes to update login status
  },
  methods: {
    checkLoginStatus() {
      const jwt = localStorage.getItem('jwt');
      const user = localStorage.getItem('user');
      if (jwt && user) {
        this.isLoggedIn = true;
        this.user = JSON.parse(user);
      } else {
        this.isLoggedIn = false;
        this.user = null;
      }
    },
    toggleDropdown() {
      this.showDropdown = !this.showDropdown;
    },
    logout() {
      localStorage.removeItem('jwt');
      localStorage.removeItem('user');
      this.isLoggedIn = false;
      this.user = null;
      this.$router.push('/login');
    },
  },
};
</script>

<style scoped>
/* Basic styling for dropdown if needed, or rely on Bootstrap */
.dropdown-menu {
  display: none; /* Hidden by default */
}
.dropdown-menu.show {
  display: block; /* Shown when 'show' class is present */
}
</style>